.showcard {
  padding: 10px 0;
  height: 100%;
  font-size: 14px;
}

.showcard__container {
  position: relative;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  height: 100%;
}

.showcard__container .showcard__image {
  border-radius: 5px;
  width: 100%;
}

.showcard__placeholder {
  height: 100%;
  background-color: #3c948b;
  width: 100%;
}

.cardflip .showcard__container {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.showcard__front {
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  height: 100%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.showcard__back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #3f3f3f;
  color: #3c948b;
  border-radius: 5px;
  font-size: 12px;
}

.showcard__back__link {
  position: absolute;
  bottom: 10%;
  left: 0;
  border-radius: 0 5px 5px 0;
  padding: 10px;
  color: white;
  text-decoration: none;
  background-color: #3c948b;
}

.showcard__back__body {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.showcard__back__name {
  text-align: center;
  padding: 5px 0;
}

.showcard__back__language {
  text-align: center;
  padding: 5px 0;
}

.showcard__back__icons {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: baseline;
  margin-bottom: 10%;
}

.showcard__back__icons svg {
  font-size: 30px;
}

.showcard__back__icons .rating {
  display: flex;
  align-items: center;
  color: #3c948b;
  margin: 0 5px;
}

.showcard__back__icons .favorites {
  position: relative;
  cursor: pointer;
  margin: 0 5px;
}

.showcard__back__icons .favorites .hiddenHeart {
  visibility: hidden;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    opacity: 0.3;
  }
  50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0.6;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    opacity: 0.3;
  }
  50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0.6;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

.showcard__back__icons .favorites .emptyHeart {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
}

.showcard__back__icons .favorites .pulsingHeart {
  -webkit-animation: pulse ease-in-out 0.2s;
          animation: pulse ease-in-out 0.2s;
  -webkit-animation-iteration-count: 2;
          animation-iteration-count: 2;
}

.showcard__back__icons .favorites svg {
  fill: #3c948b;
}

nav {
  background-color: #3f3f3f;
  color: white;
}

.top__nav {
  padding: 20px 0;
}

.nav__form {
  display: block;
  margin: 0 auto;
  width: 50%;
}

.nav__form__row {
  position: relative;
  width: 75%;
  display: inline-block;
}

.nav__form__input {
  display: inline-block;
  width: 100%;
  height: 36px;
  border-radius: 36px 0 0 36px;
  border: none;
  padding-left: 20px;
  font-family: "Lato", sans-serif;
  font-weight: 700;
}

.nav__form__input:focus {
  outline: none;
}

.nav__form label {
  position: absolute;
  pointer-events: none;
  bottom: 0px;
  height: 100%;
  width: 97%;
  right: 0px;
}

.nav__form label::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0px;
  border-bottom: 2px solid #3c948b;
  -webkit-transform: scaleX(0.1);
          transform: scaleX(0.1);
  -webkit-transform-origin: right;
          transform-origin: right;
  transition: -webkit-transform 200ms linear;
  transition: transform 200ms linear;
  transition: transform 200ms linear, -webkit-transform 200ms linear;
}

.nav__form__input:focus + label::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.nav__form__button {
  border-radius: 0 30px 30px 0;
  width: 25%;
  height: 36px;
  border: none;
  background-color: #3c948b;
  color: white;
  cursor: pointer;
}

.site__nav {
  background-color: #3c948b;
}

.site__nav__link {
