.loader__container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #3f3f3f;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.loader__container svg {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  stroke-dasharray: 251;
  /* (2PI * 40px) */
  stroke-dashoffset: 251;
  -webkit-animation: offsettozero 5s infinite linear;
          animation: offsettozero 5s infinite linear;
  fill: #3f3f3f;
  stroke: #3c948b;
  -webkit-transform: scale(3);
          transform: scale(3);
}

@-webkit-keyframes offsettozero {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes offsettozero {
  to {
    stroke-dashoffset: 0;
  }
}

